/*Card outline*/
@font-face {
  font-family: 'HelveticaNeue';
  font-style: normal;
  font-weight: normal;
  src:
    url('../fonts/HelveticaNeueLight.ttf') format('truetype');
}

@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: normal;
  src:
    url('../fonts/Roboto-Regular.ttf') format('truetype');
}

.Card-div {
	background: #fff;
	border-radius: 10px;
	margin: 10px 5px 10px 5px;
	-webkit-box-shadow: 0pt 1pt 3pt 2pt rgba(0, 0, 0, 0.2);
	        box-shadow: 0pt 1pt 3pt 2pt rgba(0, 0, 0, 0.2);
	width: 280pt;
    font-family: 'HelveticaNeue';
}

/* .Card-line-div {
	height: 0.8pt;
	background: #eee;
	margin: 0pt 5pt 0pt 5pt;
} */

.TwoColumn-div {
	height: 15pt;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
	overflow: auto;
 	padding: 10pt;
}

.Cta-div {
	height: 15pt;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	overflow: auto;
 	padding: 10pt;
}

.Cta-div > a {
	color: #3F5EFB;
	text-decoration: none;
}

/* Title card */

.Title-div {
	height: 30pt;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
	overflow: auto;
 	padding: 10pt;
  background: #eee;
}

.poster-div {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
	margin: 0pt 5pt 5pt 5pt;
}

.poster-image {
	height: 27pt;
	width: 27pt;
	margin-top: 8pt;
	margin-left: 8pt;
	margin-bottom: 5pt;
	margin-right: 5pt;
	border-radius: 50pt;
}

.poster-image-div {
    display: -ms-flexbox;
    display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
}

.poster-details {
	font-size: 12pt;
	text-align: right;
}

.poster-details .posted-by {
	display: block;
    float: left;
}

.poster-details-div {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: column;
	    flex-flow: column;
	width: 150px;
}

/*.poster-details .title {
		color: #653184;
    float: right;
}*/

.posted-timestamp {
	color: #717985;
	font-size: 8pt;
	display: inline-block;
}

.poster-details .posted-timestamp {
	color: #717985;
	font-size: 8pt;
	float: bottom;
}

.link-image {
	width: 280pt;
}

.link-description {
	font-size: 13pt;
	margin-top: 5pt;
	margin-left: 10pt;
	margin-right: 8pt;
	line-height: 16pt;
	color: #1F3158;
}

.Title {
	font-size: 17pt;
	font-weight: 500;
	color: #333;
}

.Timestamp {
	font-size: 9pt;
	font-weight: 200;
	color: #777;
}



